body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  background-color: #F7F7F7;
}

.header {
  background-color: white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1607843137);
}

.header__logo {
  font-size: 2rem;
  color: #08A6E4;
  padding: 1rem 1.56rem;
  font-weight: 300;
}

.menu__list {
  transition: all 0.3s linear;
  overflow: hidden;
  max-height: 0;
  padding: 0 1rem;
  background-color: white;
  position: absolute;
  width: 100%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1607843137);
}
@media screen and (min-width: 1200px) {
  .menu__list {
    overflow: visible;
    position: unset;
    max-height: unset;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: none;
  }
}
@media screen and (min-width: 1200px) {
  .menu__item {
    height: 100%;
    display: flex;
    justify-content: center;
  }
}
.menu__link {
  font-size: 0.69rem;
  color: #A5A5A5;
  font-family: "Open Sans", sans-serif;
  font-weight: 800;
  text-decoration: none;
  transition: 0.5s;
  display: flex;
  align-items: center;
}
@media screen and (min-width: 1200px) {
  .menu__link {
    padding: 0 1rem;
  }
}
.menu__link:hover {
  color: black;
}
@media screen and (min-width: 1200px) {
  .menu__link:hover {
    border-bottom: 2px solid #08A6E4;
  }
}
.menu__burger {
  padding: 0.5rem;
  position: absolute;
  right: 1.56rem;
  top: 1rem;
  cursor: pointer;
}
@media screen and (min-width: 1200px) {
  .menu__burger {
    display: none;
  }
}
.menu__burger span {
  display: block;
  width: 1.5rem;
  height: 2px;
  background-color: #08A6E4;
  margin-bottom: 0.35rem;
  transition: 0.5s linear;
}
.menu__burger span:first-child {
  margin-top: auto;
}

input {
  display: none;
}

input#menu:checked ~ div .menu__list {
  max-height: 10rem;
  padding: 1rem;
  margin-top: 1rem;
}
input#menu:checked ~ div .menu__burger span:nth-child(1) {
  transform: translateY(8px) rotate(225deg);
}
input#menu:checked ~ div .menu__burger span:nth-child(2) {
  transform: rotate(-225deg);
}
input#menu:checked ~ div .menu__burger span:nth-child(3) {
  opacity: 0;
}